<template>
    <button :class="theClass" type="button">
        <slot></slot>
    </button>
</template>

<script>
    export default {
        name: 'test',
        props: ['theme'],
        data() {
            return {
                theClass: 'button'
            }
        },
        created() {
            if (this.theme) {
                this.theClass = this[this.theme][this.theClass];
                console.log(this.theClass);
            }
        }
    }
</script>

<style module="red">
    @import '../css/themes/red.css';
    @import '../css/components/button.css';
</style>
 
<style module="blue"> 
    @import '../css/themes/blue.css';
    @import '../css/components/button.css';
</style>


